import React from "react";
import "../ItemList.css";

import Shirt from "./Shirt"

const shirts = [
    { path: "src/assets/t-shirt.svg" },
    { path: "src/assets/color-88C10F.svg" },
    { path: "src/assets/color-533724.svg" },
    { path: "src/assets/color-B2C7C7.svg" },
    { path: "src/assets/color-E0A39A.svg" },
];

function ShirtList() {
    return (
        <div className="container-shirt-list">
            {shirts.map((shirt, index) => (
                <Shirt
                    path={shirt.path}
                />
            ))}
        </div>
    )
}

export default ShirtList;