import React from "react";
import "../ItemList.css";

import Skin from "./Skin"

const skins = [
    { path: "src/assets/skin.svg" },
    { path: "src/assets/color-88C10F.svg" },
    { path: "src/assets/color-533724.svg" },
    { path: "src/assets/color-B2C7C7.svg" },
    { path: "src/assets/color-E0A39A.svg" },
];

function SkinList() {
    return (
        <div className="container-skin-list">
            {skins.map((skin, index) => (
                <Skin
                    path={skin.path}
                />
            ))}
        </div>
    )
}

export default SkinList;